.anaconda-app-settings-icon {
  -webkit-mask-image: var(--anaconda-rs-settings-icon);
  mask-image: var(--anaconda-rs-settings-icon);
  width: 16px;
  height: 16px;
  background: #2c303a;
  margin-left: auto;
}

.anaconda-app-settings-gray-icon {
  -webkit-mask-image: var(--anaconda-rs-settings-icon);
  mask-image: var(--anaconda-rs-settings-icon);
  width: 16px;
  height: 16px;
  background: #6a718a;
  cursor: pointer;
}

.anaconda-app-list {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.anaconda-app-div {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.anaconda-app-header {
  display: inline-flex;
  flex: 0 0 auto;
  position: relative;
  gap: 10px;
  justify-content: space-between;
}

.anaconda-app-header h3 {
  font-weight: 500;
  font-size: 34px;
  line-height: 38px;
  margin: 0;
  margin-bottom: 25px;
}

.anaconda-app-tile {
  width: 260px;
  border: 1px solid;
  border-radius: 8px;
  background-color: var(--anaconda-lms-tile-background);
  border: 1px solid var(--gray-400, #8c94a6);
  cursor: pointer;
  display: flex;
}

.anaconda-app-frame {
  align-items: flex-start;
  display: flex;
  position: relative;
  width: -webkit-fill-available;
}

.anaconda-app-gradient-published,
.anaconda-app-gradient-publishing,
.anaconda-app-gradient-failed,
.anaconda-app-gradient-inactive {
  border-radius: 6px 0px 0px 6px;
  height: 107px;
  position: relative;
  width: 6px;
}

.anaconda-app-gradient-published {
  background: #0cca4a;
}

.anaconda-app-gradient-publishing {
  background: #ffc105;
}

.anaconda-app-gradient-failed {
  background: #dc3246;
}

.anaconda-app-gradient-inactive {
  background: #8c94a6;
}

.anaconda-app-container {
  display: flex;
  flex-direction: column;
  height: 67px;
  justify-content: space-between;
  margin-right: -10px;
  padding: 12px;
  position: relative;
  width: -webkit-fill-available;
}

.anaconda-app-header-div,
.anaconda-app-status-div {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  position: relative;
  width: 225px;
  gap: 8px;
}

.anaconda-app-header-div {
  justify-content: flex-start;
}

.anaconda-app-status-div {
  justify-content: space-between;
}

.anaconda-app-subheader {
  display: flex;
  margin: 10px 0px;
}

.anaconda-app-subheader a {
  color: #43b049;
  font-weight: 700;
  text-decoration: underline;
  cursor: pointer;
}

.anaconda-app-header-text {
  color: var(--anaconda-lms-default-color);
  font-family: var(--anaconda-lms-font-family);
  font-size: 16px;
  font-weight: 700;
  text-transform: capitalize;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 165px;
}

.anaconda-app-launcher {
  display: flex;
  flex-direction: column;
  padding: 42px;
  height: calc(100% - 60px);
  overflow: auto;
}

.anaconda-app-version-published,
.anaconda-app-version-publishing,
.anaconda-app-version-inactive,
.anaconda-app-version-failed {
  background: rgba(67, 176, 73, 0.12);
  border-radius: 8px;
  padding: 5px;
  margin-top: 10px;
  width: max-content;
}

.anaconda-app-version-publishing {
  background: rgba(255, 193, 5, 0.12);
}

.anaconda-app-version-inactive {
  background: rgba(44, 48, 58, 0.12);
}

.anaconda-app-version-failed {
  background: rgba(220, 50, 70, 0.12);
}

.anaconda-app-status {
  display: flex;
  align-self: end;
}

.anaconda-app-status span {
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
}

/* Apps Error Container Styles */
.anaconda-apps-error {
  background: var(--anaconda-apps-tile-background);
  border: 0.5px solid var(--anaconda-apps-tile-border);
  border-radius: 4px;
  position: relative;
  padding: 17px 36px;
  display: flex;
  flex-direction: column;
  width: -webkit-fill-available;
}

.anaconda-apps-tile-left-border {
  position: absolute;
  width: 5px;
  height: calc(100% + 1px);
  background: var(--anaconda-apps-alert-color);
  border-radius: 4px 0 0 4px;
  top: -1px;
  left: -1px;
}

.anaconda-apps-modal .jp-Dialog-content {
  border-radius: 8px;
  background: var(--bg-color, #fff);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(5px);
  width: 520px;
  min-height: 480px;
  max-height: 520px;
  padding: 0px;
}

.anaconda-apps-modal .jp-Dialog-header,
.anaconda-versions-modal .jp-Dialog-header {
  border-bottom: 1px solid var(--gray-200, #ced2d9);
  padding: 15px 20px;
  background: var(--jp-layout-color1);
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
}

.anaconda-apps-modal .jp-Dialog-footer {
  margin: 0px;
  padding: 0px;
}

.anaconda-apps-modal .jp-Dialog-body,
.anaconda-versions-modal .jp-Dialog-body {
  overflow: hidden !important;
}

#topbar-app-counter {
  display: flex;
}

.anaconda-versions-modal .jp-Dialog-content {
  border-radius: 8px;
  background: var(--bg-color, #fff);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(5px);
  width: 806px;
  min-height: 450px;
  padding: 0px;
}

.anaconda-versions-modal .jp-Dialog-header {
  padding: 15px 20px;
  background: var(--jp-layout-color1);
}

.anaconda-versions-modal .jp-Dialog-footer {
  margin: 0px;
  padding: 0px;
}

.anaconda-versions-modal__grid-container {
  width: 470px;
  height: 470px;
}

.paDeploy svg {
  width: 24px !important;
}

.anaconda-panel-tabs {
  font-family: arial;
  margin: 0 auto;
  display: flex;
  text-align: center;
  padding: 20px;
  gap: 24px;
}

.anaconda-panel-tab-active,
.anaconda-panel-tab-inactive {
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  cursor: pointer;
}

.anaconda-panel-tab-inactive {
  color: var(--gray-400, #8c94a6);
}
.anaconda-panel-tab-active {
  border-bottom: 2px solid var(--jp-ui-font-color1);
  padding-bottom: 5px;
  color: var(--jp-ui-font-color1);
}

.anaconda-apps-preview-icon {
  -webkit-mask-image: var(--anaconda-apps-preview-icon);
  mask-image: var(--anaconda-apps-preview-icon);
  width: 20px;
  height: 20px;
  background: #2c303a;
  cursor: pointer;
}

.anaconda-apps-notebook-icon {
  -webkit-mask-image: var(--anaconda-apps-notebook-icon);
  mask-image: var(--anaconda-apps-notebook-icon);
  width: 19px;
  height: 20px;
  background: var(--jp-ui-font-color0);
  cursor: pointer;
  align-self: end;
}

.anaconda-apps-check-icon {
  -webkit-mask-image: var(--anaconda-apps-check-icon);
  mask-image: var(--anaconda-apps-check-icon);
  width: 16px;
  height: 16px;
  background: #0cca4a;
  margin-right: 8px;
}

.anaconda-versions-modal__grid-container {
  width: auto;
  padding: 0px 20px;
}

.anaconda-versions-modal .anaconda-versions-modal__grid-container {
  padding: 20px !important;
}

.anaconda-versions-modal
  .anaconda-versions-modal__grid-container
  .ag-root-wrapper {
  height: 400px !important;
}

.anaconda-versions-modal__grid-container .ag-root-wrapper {
  height: 380px !important;
  overflow: auto;
}

.anaconda-versions-modal__grid-container .ag-cell {
  display: flex;
  align-items: center;
}

.anaconda-apps-grid-action-button-cell {
  display: flex;
  align-items: center;
  height: 100%;
  gap: 12px;
}

.anaconda-apps-grid-action-background {
  border-radius: 100px;
  background: var(--buttons-secondary-secondary-button-bg, #e5e8eb);
  padding: 10px;
}

input[name="published"] {
  accent-color: rgb(77 162 97);
  width: 20px;
  height: 20px;
  transform: scale(0.9);
}

.anaconda-app-list-desc {
  font-size: 16px;
  font-weight: 400;
}

.anaconda-example-app-tile {
  width: 260px;
  height: 200px;
  border-radius: 8px;
  background-color: var(--anaconda-lms-tile-background);
  border: 1px solid var(--gray-400, #8c94a6);
  cursor: pointer;
  display: flex;
}

.anaconda-example-app-frame {
  position: relative;
  width: 260px;
  align-items: flex-start;
  display: flex;
}

.anaconda-example-app-container {
  display: flex;
  flex-direction: column;
  padding: 11px;
  height: -webkit-fill-available;
}

.anaconda-example-app-gradient {
  border-radius: 6px 0px 0px 6px;
  height: 200px;
  width: 6px;
  position: relative;
  background: #0cca4a;
}

.anaconda-example-app-preview {
  width: 230px;
  height: 120px;
  border-radius: 4px;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}

.anaconda-example-app-title {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 10px;
}
.anaconda-example-app-owner {
  width: 25px;
  height: 20px;
  border-radius: 90px;
  background-color: #43b049;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-top: 10px;
  cursor: pointer;
}

.anaconda-example-app-action-div {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  position: relative;
  width: 230px;
  gap: 8px;
}

.anaconda-example-app-actions {
  display: flex;
  align-self: end;
  justify-content: flex-end;
  width: -webkit-fill-available;
  gap: 10px;
}

.anaconda-app-resources-header {
  display: flex;
  width: -webkit-fill-available;
  flex-direction: row;
}

.anaconda-app-resources-collapsible-div {
  align-self: center;
  margin-right: 30px;
}

.anaconda-app-resources-collapsible {
  display: flex;
  gap: 10px;
  font-size: 16px;
  color: #6c5af6;
  cursor: pointer;
}

.anaconda-app-resources-collapsible span {
  align-self: end;
}
